import { OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { CustomCardService } from '../../../services/cards/v1-card.services';
import { HttpService } from '../../../services/http/http.service';
var FloatSocialComponent = /** @class */ (function () {
    function FloatSocialComponent(router, service, _http) {
        this.router = router;
        this.service = service;
        this._http = _http;
        this.hide = true;
        this.socialmedia = [];
        this.dataMessage = [];
    }
    FloatSocialComponent.prototype.clickOnSubmit = function (formulario) {
        var _this = this;
        this.dataMessage = [];
        formulario['webform_id'] = 'how_can_we_help_you';
        this._http.post('webform_rest/submit?_format=json', formulario, {
            'Content-Type': 'application/json',
            'X-CSRF-Token': this._token
        })
            .subscribe(function (datos) {
            _this.ubicar();
            if (datos.error) {
                for (var key in datos.error) {
                    _this.dataMessage.push({
                        visible: true,
                        status: 'error',
                        message: datos.error[key]
                    });
                }
            }
            else if (datos.sid) {
                $('#modal-form')[0].reset();
                _this.dataMessage.push({
                    visible: true,
                    status: 'success',
                    message: 'Respuesta satisfactoria'
                });
            }
        });
    };
    FloatSocialComponent.prototype.ubicar = function () {
        var x = document.querySelector('.modal-header');
        if (x) {
            x.scrollIntoView({ block: 'start', inline: 'start', behavior: 'smooth' });
        }
    };
    FloatSocialComponent.prototype.ngOnInit = function () {
        var _this = this;
        this.languagueBrowser = this.service.getLanguageBrowser();
        if (this.languagueBrowser !== 'es') {
            this.ruta = '/' + this.languagueBrowser + '/contact-us';
        }
        else if (this.languagueBrowser === 'es') {
            this.ruta = '/contactenos';
        }
        this.getFloatSocialItems();
        this.getModalData();
        this._http.get('rest/session/token', {}, { responseType: 'text' }).subscribe(function (response) {
            _this._token = response;
        });
        $(function () {
            $('#openModal').click(function (e) {
                $('a[href$="#contactusModal"]').on('click', function () {
                    $('#contactusModal').modal('show');
                });
                e.preventDefault();
            });
        });
    };
    FloatSocialComponent.prototype.onWindowScroll = function () {
        var windowHeight = $(window).height() + $(document).scrollTop();
        var contenido2 = $('#footer-block').offset();
        contenido2 = contenido2.top;
        if (windowHeight >= contenido2) {
            $('#float-social-block').css('transition', 'all 300ms ease');
            $('#float-social-block').css('opacity', '0');
            //this.hide = false;
        }
        else {
            //this.hide = true;
            $('#float-social-block').css('transition', 'all 300ms ease');
            $('#float-social-block').css('opacity', '1');
        }
        /* if ($(window).height() + $(document).scrollTop() === $(document).height()) {
          this.hide = false;
        } else {
          this.hide = true;
        } */
    };
    FloatSocialComponent.prototype.getFloatSocialItems = function () {
        var _this = this;
        this.service.getCustomCardInformation('contactcard').subscribe(function (items) {
            _this.socialmedia = items.others;
            if (items.header) {
                _this.positionSidebar = items.header[2].data.position;
            }
        });
    };
    FloatSocialComponent.prototype.getModalData = function () {
        var _this = this;
        this.service.getCustomForm('how_can_we_help_you').subscribe(function (params) {
            _this.title_form = params.title['#markup'];
            _this.descrip_form = params.subtitle['#markup'];
            _this.footer_text = params.top_mensaje_footer['#markup'];
            _this.footer_contact = params.botton_footer_message['#markup'];
        });
    };
    return FloatSocialComponent;
}());
export { FloatSocialComponent };
