import { OnInit, ElementRef } from '@angular/core';
import 'rxjs/add/operator/map';
import { HttpService } from '../../../services/http/http.service';
import { CustomCardService } from 'src/app/services/cards/v1-card.services';
import { HttpClient } from '@angular/common/http';
import { SelectComponent } from 'ng2-select';
import { FormBuilder } from '@angular/forms';
import { NgbPopoverConfig } from '@ng-bootstrap/ng-bootstrap';
var WorkusComponent = /** @class */ (function () {
    function WorkusComponent(_http, _service, http_pais, fb, config) {
        this._http = _http;
        this._service = _service;
        this.http_pais = http_pais;
        this.fb = fb;
        this.hidden = false;
        this.usuario = {
            nombre: null,
            email: null
        };
        this.value = {};
        this.file = {};
        this.dataMessage = [];
        this.list = [];
        this.bandPais = [];
        this.listPais = [];
        this._disabledV = '0';
        this.disabled = false;
        this.pais = '';
        this.checked = false;
        this.form = this.fb.group({
            hojav: null
        });
        this.file = {
            name: '',
            mime: '',
            blob: '',
        };
        this.hojaWU = '';
        this.valido = false;
        this.hover_buttom = 'Faltan datos por llenar';
        this.hojaWU = 'Subir Fichero';
        this.languagueBrowser = '';
        this.ruta = '';
    }
    WorkusComponent.prototype.onSubmit = function (formulario) {
        var _this = this;
        if (this.captcha_form === 'null' || this.captcha_form === undefined) {
            this.valido = false;
            this.hover_buttom = 'Faltan datos por llenar';
        }
        if (this.valido === true) {
            formulario['fileupload'] = this.file;
            this.dataMessage = [];
            formulario['webform_id'] = 'work_with_us';
            formulario['captcha'] = this.captcha_form;
            this._http.post('webform_rest/submit?_format=json', formulario, {
                'Content-Type': 'application/json',
                'X-CSRF-Token': this._token
            })
                .subscribe(function (datos) {
                _this.ubicar();
                if (datos.error) {
                    for (var key in datos.error) {
                        _this.dataMessage.push({
                            visible: true,
                            status: 'error',
                            message: datos.error[key]
                        });
                    }
                }
                else if (datos.sid) {
                    jQuery('#formulario_contactoWS')[0].reset();
                    _this.ngSelectW.active = [];
                    _this.pais = '';
                    _this.hojaWU = 'Subir Fichero';
                    _this.dataMessage.push({
                        visible: true,
                        status: 'success',
                        message: 'Formulario de contacto enviado exitosamente. Nos podremos en contacto con usted!'
                    });
                }
            });
        }
        else {
            this.dataMessage = [];
        }
    };
    WorkusComponent.prototype.mostrarDatosWS = function (id) {
        $('#' + id.target.id).addClass('ocultar-placeholder');
    };
    WorkusComponent.prototype.ocultarDatosWS = function (id) {
        $('#' + id.target.id).removeClass('ocultar-placeholder');
    };
    WorkusComponent.prototype.ngOnInit = function () {
        var _this = this;
        this.languagueBrowser = this._service.getLanguageBrowser();
        if (this.languagueBrowser !== 'es') {
            this.ruta = '/' + this.languagueBrowser + '/contact-us';
        }
        else if (this.languagueBrowser === 'es') {
            this.ruta = '/contactenos';
        }
        this._http.get('rest/session/token', {}, { responseType: 'text' }).subscribe(function (response) {
            _this._token = response;
        });
        this.getPopoverService();
        this.getPopoverQuestionService();
        this.getPaises();
        this.getForm();
    };
    WorkusComponent.prototype.getForm = function () {
        var _this = this;
        this._service.getCustomForm('work_with_us').subscribe(function (params) {
            var elementLayout = '';
            var listLayout = [];
            // Se obtienen de la respuesta del servicio los layout y elementos del formulario, se almacan en un array.
            for (var index in params) {
                elementLayout = index;
                if (elementLayout === 'fileupload') {
                    listLayout.push(params[index]);
                }
                else if (elementLayout.indexOf('#') == -1) {
                    listLayout.push(params[index]);
                }
            }
            // Se insertan en el array listLayout los campos del formulario que pertenecen a cada layout y los que no pertenecen
            for (var index in listLayout) {
                var campoForm = [];
                var cont = 0;
                if (listLayout[index]) {
                    if (listLayout[index]['#type'] !== 'managed_file') {
                        for (var indexj in listLayout[index]) {
                            if (indexj.indexOf('#') == -1) {
                                cont++;
                                campoForm.push(listLayout[index][indexj]);
                            }
                        }
                        if (cont != 0) {
                            listLayout[index] = [];
                            for (var indexz in campoForm) {
                                listLayout[index].push(campoForm[indexz]);
                            }
                        }
                    }
                }
                // Array de campos que conforman el formulario
                _this.elementoForm = listLayout;
            }
        });
    };
    WorkusComponent.prototype.resolved = function (captchaResponse) {
        this.captcha_form = "" + captchaResponse;
        this.valido = true;
        this.hover_buttom = 'Enviar datos';
    };
    WorkusComponent.prototype.onFileChange = function (event) {
        var _this = this;
        this.file['name'] = event.target.files[0].name;
        this.file['mime'] = event.target.files[0].type;
        this.file['blob'] = '';
        if (event.target.files.length > 0) {
            //this.file = event.target.files[0];
            //this.form.get('hojav').setValue(this.file);
            this.hojaWU = event.target.files[0].name;
            var reader = new FileReader();
            var ourThis = this;
            reader.onloadend = function (e) {
                _this.file['blob'] = ourThis._service.base64ArrayBuffer(e.target['result']);
            };
            reader.onerror = function (e) {
                console.log(e.target['error']);
            };
            reader.readAsArrayBuffer(event.target.files[0]);
        }
    };
    WorkusComponent.prototype.verificaValidTouched = function (campo) {
        return !campo.valid && campo.touched;
    };
    WorkusComponent.prototype.getPopoverService = function () {
        var _this = this;
        this._service.getCustomContentBasicPage('c00ea48d-1ce3-4bba-b65e-d57daf71cf4a').subscribe(function (params) {
            _this.titulo = params.title;
            _this.list = params.body;
        });
    };
    WorkusComponent.prototype.getPopoverQuestionService = function () {
        var _this = this;
        this._service.getCustomContentBasicPage('dc60d310-0a96-41a3-9522-951af2b9f340').subscribe(function (params) {
            _this.tituloQuestion = params.title;
            _this.listQuestion = params.body;
        });
    };
    WorkusComponent.prototype.aplicaCssErro = function (campo) {
        return {
            'has-error': this.verificaValidTouched(campo),
            'has-feedback': this.verificaValidTouched(campo)
        };
    };
    WorkusComponent.prototype.ubicar = function () {
        var x = document.querySelector('.tab');
        if (x) {
            x.scrollIntoView({ block: 'start', inline: 'start', behavior: 'smooth' });
        }
    };
    WorkusComponent.prototype.getPaises = function () {
        var _this = this;
        this.http_pais.get('https://restcountries.eu/rest/v2/all').subscribe(function (params) {
            for (var index = 0; index < params.length; index++) {
                _this.bandPais.push(params[index].name);
            }
            _this.listPais = _this.bandPais;
        });
    };
    Object.defineProperty(WorkusComponent.prototype, "disabledV", {
        // Metodos del select
        get: function () {
            return this._disabledV;
        },
        set: function (value) {
            this._disabledV = value;
            this.disabled = this._disabledV === '1';
        },
        enumerable: true,
        configurable: true
    });
    WorkusComponent.prototype.selectedPais = function (value) {
        this.pais = value.text;
    };
    WorkusComponent.prototype.removedPais = function (value) {
        this.pais = '';
    };
    WorkusComponent.prototype.typedPais = function (value) {
    };
    WorkusComponent.prototype.refreshValuePais = function (value) {
        this.pais = value.text;
    };
    return WorkusComponent;
}());
export { WorkusComponent };
