import { OnInit, EventEmitter } from '@angular/core';
import { CustomCardService } from 'src/app/services/cards/v1-card.services';
var CarouselItem2Component = /** @class */ (function () {
    function CarouselItem2Component(_cardService) {
        this._cardService = _cardService;
        this.arrayBox = [];
        this.casClient = 0;
        this.propagar = new EventEmitter();
        this.clients = [];
        this.visible = false;
        this.show = false;
        this.showPhone = false;
    }
    CarouselItem2Component.prototype.ngOnInit = function () {
        this.datosMenu = {
            label: 'CAROUSEL',
            id: 'a6',
            url: '/imedical'
        };
        //this.propagar.emit(this.datosMenu);
        this.getOurClientsItems();
        this.clients = [0, 1, 2, 3, 4, 5];
        this.carocarouselTile = {
            grid: { xs: 1, sm: 2, md: 4, lg: 4, all: 0 },
            slide: 1,
            speed: 400,
            animation: 'lazy',
            point: {
                visible: false,
            },
            load: 4,
            touch: true,
            easing: 'ease',
            loop: false,
        };
    };
    CarouselItem2Component.prototype.getOurClientsItems = function () {
        var _this = this;
        if (this.type === 'home') {
            this.visible = true;
            this.flagUbication = 'home';
            this._cardService.getCustomCardInformation('clientscard').subscribe(function (items) {
                items.data = _this._cardService.addImageField(items.data, ['field_image']);
                _this.clients = items.data;
                _this.titleClients = items.header[0].data.title;
                _this.clients = Object.keys(items.data).map(function (key) { return items.data[key]; });
                _this.clientsDesktop = Object.keys(items.data).map(function (key) { return items.data[key]; });
                switch (_this.clientsDesktop.length) {
                    case 1:
                        _this.oneItems = true;
                        break;
                    case 2:
                        _this.twoItems = true;
                        break;
                    case 3:
                        _this.threeItems = true;
                        break;
                }
                _this.arrayBox = _this._cardService.organizeInfoForCarousel(_this.clients);
            });
        }
        else {
            this.principalClient$ = this._cardService.getCustomInfoIM('allproductsandservicescard_2');
            this.principalClient$.subscribe(function (items) {
                _this.principalClient = _this._cardService.clone(items);
                _this.principalClient = _this._cardService.getFilterPrincipalType(_this.principalClient, 'field_clients', _this.type);
                _this._cardService.getCustomInfoIM('imedicalclients').subscribe(function (itemsw) {
                    itemsw = _this._cardService.getFilterLists(_this.principalClient, itemsw);
                    _this.titleClients = itemsw.header[0].data.title;
                    itemsw.data = _this._cardService.addImageField(itemsw.data, ['field_image']);
                    var list_items = _this._cardService.clone(itemsw.data);
                    _this.clients = list_items;
                    _this.clientsDesktop = list_items;
                    _this.clients = _this._cardService.clone(_this.clients);
                    _this.casClient = _this.casClient + 1;
                    switch (_this.clients.length) {
                        case 1:
                            _this.oneItems = true;
                            break;
                        case 2:
                            _this.twoItems = true;
                            break;
                        case 3:
                            _this.threeItems = true;
                            break;
                    }
                    _this.arrayBox = _this._cardService.organizeInfoForCarousel(_this.clients);
                    if (_this.clients.length !== 0) {
                        _this.visible = true;
                        _this.datosMenu = {
                            label: 'CAROUSEL',
                            id: 'a6',
                            url: '/imedical'
                        };
                        _this.propagar.emit(_this.datosMenu);
                    }
                });
            });
        }
    };
    CarouselItem2Component.prototype.showImage = function () {
        $('.images img').css('transform', 'translate(0,0)');
        $('.images img').css('opacity', '1');
    };
    CarouselItem2Component.prototype.showImagePhone = function () {
        $('.images img').css('transform', 'translate(0,0)');
        $('.images img').css('opacity', '1');
    };
    return CarouselItem2Component;
}());
export { CarouselItem2Component };
